import { graphql, useStaticQuery } from 'gatsby'
import { IonicLogoGithub } from 'maeng-icons'
import { gitHubIconStyle } from './github-icon.styles'

const GitHubIcon = () => {
  const {
    site: {
      siteMetadata: {
        social: { github },
      },
    },
  } = useStaticQuery(graphql`
    query GithubQuery {
      site {
        siteMetadata {
          social {
            github
          }
        }
      }
    }
  `)
  return (
    <a
      href={github}
      className="github"
      css={gitHubIconStyle}
      aria-label="GitHub"
      target="_blank"
      rel="noopener noreferrer"
    >
      <IonicLogoGithub width={38} height={38} />
    </a>
  )
}

export default GitHubIcon
