import React, { useMemo } from 'react'
import { GatsbyImage } from 'gatsby-plugin-image'
import { Badge } from 'daisyui-custom'
import classNames from 'classnames'
import useGatsbyImage from '@/hooks/useGatsbyImage'
import { addClass } from '@/utils/dom'
import { INTERSECTION_OBSERVER_ROOT_MARGIN } from '@/constants'
import useIntersectionObserver from '@/hooks/useIntersectionObserver'

const Projects = () => {
  useIntersectionObserver(
    (entries: IntersectionObserverEntry[]) => {
      return entries
        .filter(({ isIntersecting }) => isIntersecting)
        .forEach(({ target }) => addClass(target as HTMLElement, 'observed-shown'))
    },
    { rootMargin: INTERSECTION_OBSERVER_ROOT_MARGIN },
  )

  const [maengDesign, bmart, seoul13] = useGatsbyImage([
    'maeng-design.png',
    'bmart.png',
    'seoul13.png',
  ])

  const projects = useMemo(
    () => [
      {
        link: 'https://maeng-design-docs.netlify.app',
        image: maengDesign,
        details: {
          title: ' [개인] 디자인 시스템, Maeng Design',
          term: '(22.02.13 v1.5.2)',
          description:
            '디자인 시스템 Maeng Design은 React 프로젝트를 위한 사용자 인터페이스 사양을 통일하고, 설계 차이와 구현에 필요한 불필요한 비용을 낮추며, 설계 및 프론트엔드 개발 자원을 지원하기 위해 만들어졌습니다.',
          attribution:
            '다양한 컴포넌트를 디자인하고 개발하여 오픈 소스로 배포하였습니다. API 참고를 위한 Docs와 UI 검증을 위한 Storybook도 함께 배포하였습니다.',
          tags: ['React', 'Typescript', 'Emotion', 'Storybook', 'Rollup', 'Gatsby', 'Netlify'],
        },
        github: 'https://github.com/maeng2418/Maeng-Design',
      },
      {
        link: 'https://seoul13.com',
        image: seoul13,
        details: {
          title: ' [개인] 서울일삼',
          term: '(20.10.14 v2.0.0)',
          description:
            '회의실 대여 예약사이트 제작하였습니다. 직접 서비스를 기획하고 디자인하였으며 온라인 판매 서비스를 위한 상표등록, PG사 가입, 통신판매업신고 등 개발 외의 사업 운영을 위한 전반적인 인프라를 구축하였습니다.',
          attribution:
            '추가로 기존 React만으로 짜여진 코드를 Next.js로 변경, MongoDB를 MySQL로 교체, 달력 라이브러리 직접 구현, 타입스크립트 적용 등 서비스 개선을 위한 리뉴얼을 하였습니다.',
          tags: [
            'React',
            'Typescript',
            'NextJS',
            'Express',
            'AWS',
            'JWT',
            'OAuth',
            'Passport',
            'MongoDB',
            'MySQL',
          ],
        },
        github: 'https://github.com/maeng2418/SEOUL13_edition2',
      },
      {
        link: 'https://github.com/woowa-techcamp-2020/bmart-5',
        image: bmart,
        details: {
          title: ' [팀] B 마트 모바일 웹',
          term: '(20.08.28 v1.0.0)',
          description:
            '우아한 테크 캠프 멤버들과 배달의 민족 내부 서비스 중 하나인 B마트를 NextJS를 활용하여 구현하였습니다. ',
          attribution:
            '함수형 컴포넌트 형태 개발, 상태(State) 관리를 위한 useState와 Context API 활용, Styled Component를 이용한 스타일링, 케로셀 베너를 구현, Passport + Oauth + JWT를 이용한 로그인 구현',
          tags: [
            'React',
            'Typescript',
            'NextJS',
            'Storybook',
            'Express',
            'AWS',
            'JWT',
            'OAuth',
            'Passport',
            'Github Action',
            'MySQL',
          ],
        },
        github: 'https://github.com/woowa-techcamp-2020/bmart-5',
      },
    ],
    [bmart, maengDesign, seoul13],
  )

  return (
    <section className="mb-12">
      <h2 className="text-3xl font-extrabold pt-5 pb-8">Projects</h2>
      <div className="flex flex-col gap-10">
        {projects.map(
          ({ link, image, details: { title, term, description, attribution, tags }, github }) => (
            <div
              className={classNames(
                'flex flex-col gap-4 justify-between',
                'observed observed-hidden',
                'md:flex-row',
              )}
              key={title}
            >
              <a
                className={classNames('flex flex-col grow-1 shrink-1 basis-[90%]', 'md:flex-row')}
                href={link}
                target="_blank"
                rel="noopener noreferrer"
              >
                <div
                  className={classNames(
                    'block items-center min-w-[4rem] max-w-[6rem] h-full',
                    'md:flex',
                  )}
                >
                  {image && (
                    <GatsbyImage
                      className="object-contain aspect-square bg-white rounded-xl shadow-lg border border-solid border-[#f0f0f0]"
                      image={image}
                      alt={title}
                    />
                  )}
                </div>
                <div className={classNames('ml-4 mt-4', 'md:mt-0')}>
                  <h5 className="text-lg flex items-center gap-1 mb-1">
                    <span className="font-semibold">{title}</span>
                    <span className="font-semibold text-xs text-[#8a8e94]">{term}</span>
                  </h5>
                  <div className="text-base mb-1">{description}</div>
                  <div className="text-sm text-[#767b83] mb-1">{attribution}</div>
                  <div className="flex flex-wrap gap-1">
                    {tags.map((tag) => (
                      <Badge size="sm" color="ghost" key={tag}>
                        {tag}
                      </Badge>
                    ))}
                  </div>
                </div>
              </a>
              <a
                href={github}
                className={classNames(
                  'flex items-center justify-center flex-basis basis-[5%] bg-[#242a2e] rounded-md cursor-pointer p-2',
                  'md:bg-transparent md:rounded-none md:p-0',
                )}
              >
                <div
                  className={classNames(
                    "w-6 h-6 bg-[url('../assets/images/github-white.png')] bg-contain bg-no-repeat",
                    "md:bg-[url('../assets/images/github.png')]",
                  )}
                />
              </a>
            </div>
          ),
        )}
      </div>
    </section>
  )
}

export default Projects
