import { createRef, useEffect } from 'react'
import { getBody } from '@/utils/dom'

interface UtterancesProps {
  repo: string
}

const src = 'https://utteranc.es/client.js'
const branch = 'master'
const DARK_THEME = 'photon-dark'
const LIGHT_THEME = 'github-light'

const Utterances = ({ repo }: UtterancesProps) => {
  const rootElm = createRef<HTMLDivElement>()

  useEffect(() => {
    const isDarkTheme = getBody()?.getAttribute('data-theme') === 'dark'
    const utterances = document.createElement('script')
    const utterancesConfig = {
      src,
      repo,
      branch,
      theme: isDarkTheme ? DARK_THEME : LIGHT_THEME,
      label: 'comment',
      async: true,
      'issue-term': 'pathname',
      crossorigin: 'anonymous',
    }

    Object.keys(utterancesConfig).forEach((configKey) => {
      const key = configKey as keyof typeof utterancesConfig
      // @ts-ignore
      utterances.setAttribute(key, utterancesConfig[key])
    })
    rootElm.current?.appendChild(utterances)
  }, [])

  return <div className="utterances" ref={rootElm} />
}

export default Utterances
