import type { MouseEventHandler } from 'react'
import { FacebookIcon } from './facebook-icon'
import { socialShareStyle } from './social-share.styles'

interface SocialShareProps {
  title: string
  author: string
}

const SocialShare = ({ title, author }: SocialShareProps) => {
  const onClickFacebookIcon: MouseEventHandler = (e) => {
    e.preventDefault()
    window.FB.ui({
      method: 'share',
      mobile_iframe: true,
      href: window.location.href,
      quote: `Recommend on "${title}" written by @${author}`,
    })
  }

  return (
    <div className="social-share" css={socialShareStyle}>
      <FacebookIcon onClick={onClickFacebookIcon} />
    </div>
  )
}

export default SocialShare
