import { IonicSearch } from 'maeng-icons'
import { IconButton, TextField } from 'mui-custom'
import { ChangeEvent, FormEvent, useCallback, useState } from 'react'
import useUrlParamsPost from '@/hooks/useUrlParamsPost'

const SearchField = () => {
  const { params, setParams } = useUrlParamsPost()
  const [query, setQuery] = useState(params.keyword || '')

  const handleSubmit = useCallback(
    (e: FormEvent) => {
      e.preventDefault()
      setParams({ ...params, keyword: query })
    },
    [params, query, setParams],
  )

  const handleInput = useCallback((e: ChangeEvent<HTMLInputElement>) => {
    e.preventDefault()
    setQuery(e.target.value)
  }, [])

  return (
    <form onSubmit={handleSubmit}>
      <TextField
        size="small"
        variant="outlined"
        placeholder="Search..."
        InputProps={{
          endAdornment: (
            <IconButton type="submit">
              <IonicSearch />
            </IconButton>
          ),
        }}
        onChange={handleInput}
      />
    </form>
  )
}

export default SearchField
