import { v4 as uuidv4 } from 'uuid'
import momentJS from 'moment'

export {
  merge,
  uniq,
  omit,
  isEqual,
  cloneDeep,
  range,
  uniqueId,
  debounce,
  assign,
  pick,
  partial,
} from 'lodash-es'

export type rawType =
  // 필요한 타입은 더 추가해 주세요.
  | 'Undefined'
  | 'Null'
  | 'String'
  | 'Number'
  | 'Boolean'
  | 'Array'
  | 'Object'
  | 'Function'
  | 'Promise'
  | 'Date'
  | 'Map'
  | 'Set'
  | 'Symbol'
  | 'FormData'
  | 'File'
  | 'RegExp'

export const toRawType = (v: any): rawType => {
  return Object.prototype.toString.call(v).slice(8, -1) as rawType
}

export const uuid = uuidv4

export const moment = momentJS
