import { useCallback, useMemo } from 'react'
import { navigate } from 'gatsby'
import { cloneDeep } from 'maeng-utils/utils'
import { toQueryString } from 'maeng-utils/url'
import useQueryString from './useQueryString'

// URL이 변경되면, 해당 URL의 쿼리 파라미터를 읽어서, 검색어를 가져온다.
// 활용 범위는, 필터라던가, 검색어 조회라던가, 페이지네이션 등등
const useUrlParamsPost = () => {
  const query = useQueryString()

  const baseParams = useMemo(() => ({ keyword: '' }), [])

  const params = useMemo(
    () => ({
      keyword: (query.keyword ? query.keyword : baseParams.keyword) as string,
    }),
    [baseParams.keyword, query.keyword],
  )

  // const isValidParams = schema.isValidSync(params)
  const isValidParams = useMemo(() => typeof params.keyword === 'string', [params])

  const setParams = useCallback(
    (partialParams: { keyword: string }) => {
      const queryString = { ...params, ...partialParams }
      const search = toQueryString(queryString)
      navigate(`/search?${search}`, { replace: true })
    },
    [params],
  )

  return {
    baseParams: cloneDeep(baseParams),
    params,
    setParams,
    isValidParams,
  }
}

export default useUrlParamsPost
